@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@700&display=swap);
.container-display {
  width: 100%;
  text-align: end;
  padding: 6px 30px;
  border-radius: 10px;
  margin-bottom: 20px;
  font-size: 1.4rem;
  overflow: hidden;
  max-width: 520px;
}

.container-display.Blue {
  background-color: hsl(224, 36%, 15%);
  color: hsl(0, 0%, 100%);
}

.container-display.White {
  background-color: hsl(0, 0%, 93%);
  color: hsl(60, 10%, 19%);
}

.container-display.Purple {
  background-color: hsl(268, 71%, 12%);
  color: hsl(52, 100%, 62%);
}

.button {
  border-radius: 10px;
  border-top: none;
  border-right: none;
  border-left: none;
  font-size: 34px;
  height: 60px;
  padding-top: 10px;
}

@media screen and (min-width: 375px) and (max-width: 540px) {
  .button {
    font-size: 25px;
    border-radius: 5px;
  }
}

.button:hover {
  cursor: pointer;
}

.button.Blue {
  background-color: hsl(30, 25%, 89%);
  border-bottom: 5px solid hsl(28, 16%, 65%);
  color: hsl(221, 14%, 31%);
}

.button.Blue:active {
  background-color: hsl(221, 14%, 31%);
  color: hsl(30, 25%, 89%);
}

.button.Blue.equal:active {
  background-color: hsl(30, 25%, 89%);
  color: hsl(6, 63%, 50%);
}

.button.Blue.delete:active,
.button.Blue.reset:active {
  background-color: hsl(30, 25%, 89%);
  color: hsl(224, 21%, 50%);
}

.button.White {
  background-color: hsl(45, 7%, 89%);
  border-bottom: 5px solid hsl(35, 11%, 61%);
  color: hsl(60, 10%, 19%);
}

.button.White:active {
  background-color: hsl(60, 10%, 19%);
  color: hsl(45deg 7% 89%);
}

.button.White.equal:active {
  background-color: hsl(30, 25%, 89%);
  color: hsl(25, 98%, 40%);
}

.button.White.delete:active,
.button.White.reset:active {
  background-color: hsl(30, 25%, 89%);
  color: hsl(185, 42%, 37%);
}

.button.Purple {
  background-color: hsl(268, 47%, 21%);
  border-bottom: 5px solid hsl(290, 70%, 36%);
  color: hsl(52, 100%, 62%);
}

.button.Purple:active {
  background-color: #ffe53d;
  color: #341c4f;
}

.button.Purple.equal:active {
  background-color: hsl(198, 20%, 13%);
  color: hsl(176, 100%, 44%);
}

.button.Purple.delete:active,
.button.Purple.reset:active {
  background-color: hsl(30, 25%, 89%);
  color: hsl(285, 91%, 52%);
}

.button.delete,
.button.reset,
.button.equal {
  font-size: 20px;
}

.button.reset,
.button.equal {
  grid-column: span 2;
}

.button.Blue.delete,
.button.Blue.reset {
  background-color: hsl(224, 21%, 50%);
  color: hsl(30, 25%, 89%);
  border-bottom: 4px solid hsl(224, 28%, 35%);
}

.button.Blue.equal {
  background-color: hsl(6, 63%, 50%);
  border-bottom: 4px solid hsl(6, 70%, 34%);
  color: hsl(30, 25%, 89%);
}

.button.White.delete,
.button.White.reset {
  background-color: hsl(185, 42%, 37%);
  color: hsl(30, 25%, 89%);
  border-bottom: 4px solid hsl(185, 58%, 25%);
}

.button.White.equal {
  background-color: hsl(25, 98%, 40%);
  border-bottom: 4px solid hsl(25, 99%, 27%);
  color: hsl(0, 0%, 100%);
}

.button.Purple.delete,
.button.Purple.reset {
  background-color: hsl(281, 89%, 26%);
  color: hsl(30, 25%, 89%);
  border-bottom: 4px solid hsl(285, 91%, 52%);
}

.button.Purple.equal {
  background-color: hsl(176, 100%, 44%);
  border-bottom: 4px solid hsl(177, 92%, 70%);
  color: hsl(198, 20%, 13%);
}

.Header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 10px;
}

.Header.Blue {
  color: #fff;
}

.Header.White {
  color: hsl(60, 10%, 19%);
}

.Header.Purple {
  color: hsl(52, 100%, 62%);
}

.Header-Theme {
  display: grid;
  grid-template-columns: 0.8fr 1fr;
  grid-template-rows: 1fr 1fr;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
  font-size: 12px;
  letter-spacing: 1px;
}

.Header-Numbers {
  letter-spacing: 13px;
  justify-self: end;
  font-size: 10px;
  margin-left: 10px;
}

.slider {
  display: -webkit-flex;
  display: flex;
  border-radius: 50px;
  width: 70px;
  padding: 3px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.slider.Blue,
.switch.Blue.false:before {
  background-color: hsl(223, 31%, 20%);
}
.slider.White,
.switch.White.false:before {
  background-color: hsl(0, 5%, 81%);
}
.slider.Purple,
.switch.Purple.false:before {
  background-color: hsl(268, 71%, 12%);
}

.switch:before {
  content: "";
  display: block;
  width: 13px;
  height: 13px;
  top: 0;
  left: 0;
  border-radius: 50px;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  cursor: pointer;
}

.switch.Blue.true:before {
  background-color: hsl(6, 63%, 50%);
}

.switch.White.true:before {
  background-color: hsl(25, 98%, 40%);
}

.switch.Purple.true:before {
  background-color: hsl(176, 100%, 44%);
}

.container-keys {
  display: grid;
  grid-template-columns: repeat(4, 100px);
  grid-gap: 20px;
  padding: 30px;
  border-radius: 10px;
}

@media screen and (min-width: 375px) and (max-width: 540px) {
  .container-keys {
    grid-template-columns: repeat(4, 60px);
    grid-gap: 12px;
  }
}

.container-keys.Blue {
  background-color: hsl(223, 31%, 20%);
}

.container-keys.White {
  background-color: hsl(0, 5%, 81%);
}

.container-keys.Purple {
  background-color: hsl(268, 71%, 12%);
}

body {
  display: -webkit-flex;
  display: flex;
  height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 2px;
  background-color: hsl(222, 26%, 31%);
}

* {
  box-sizing: border-box;
  font-family: "Spartan", "sans-serif";
}

